body {
	font: 14px "Century Gothic", Futura, sans-serif;
	margin: 20px;
	justify-content: center;
	background: linear-gradient(217deg, rgba(255,0,0,.8), rgba(255,0,0,0) 70.71%),
            linear-gradient(127deg, rgba(0,255,0,.8), rgba(0,255,0,0) 70.71%),
            linear-gradient(336deg, rgba(0,0,255,.8), rgba(0,0,255,0) 70.71%);

}
 ol, ul {
 	padding-left: 30px;
 }
  .board-row:after {
  	clear: both;
  	content: "";
  	display: table;
  }

  .status {
  	margin-bottom: 10px;
  }

  .square {
  	background: #fff;
  	border: 1px solid #999;
  	float: left;
  	font-size: 24px;
  	font-weight: bold;
  	line-height: 34px;
  	height: 34px;
  	margin-right: -1px;
  	margin-top: -1px;
  	padding: 0;
  	text-align: center;
  	width: 34px;
  }

  .square:focus {
  	outline: none;
  }

  .kbd-navigation .square:focus {
  	background: #ddd;
  }

  .game {
  	display: flex;
  	flex-direction: row;
  }

  .game-info {
  	margin-left: 20px;
  }